import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			date1: currentDate,
			date2: currentDate,
			buysData : [],
			sellData : []
		};
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onLoad(){
		this.queryHistory();
	},
	filters: {
　　　　 timeZhuan(value) {
			return value.substring(0,10);
　　　　 }
　　},
	methods: {
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		queryHistory(){
			uni.showLoading({
				mask:true
			})
			http.get('transaction/findTransactionRecord',{phone:this.$store.state.userInfo.phone,starTime:this.date1,endTime:this.date2,flag:'S'}).then((res)=>{
				if(res.data.data.buys.length == 0 && res.data.data.sells.length == 0){
					uni.showModal({
						title : '提示',
						content : '今日无记录',
						showCancel : false,
					})
					return;
				}
				this.buysData = res.data.data.buys;
				this.sellData = res.data.data.sells;
			})
		}
	}
}
